@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar .navbar-brand .nav-link{color:rgba(255,255,255,0.5)}.navbar .navbar-brand .nav-link.active{color:white}.navbar .nav-item .nav-link{font-size:1.1rem}.navbar .dropdown-menu.show{background-color:#343a40 !important}.navbar .dropdown-menu .nav-link{padding:10px}.navbar .dropdown-menu .nav-link.active{background-color:#666 !important}

.full-background{height:100vh;background-position:center;background-repeat:no-repeat;background-size:cover}

.pageContainer.home header{position:absolute;width:100%}.pageContainer.home header .navbar{background:rgba(0,0,0,0.3) !important}.pageContainer.home .contentWrapper{padding:0px !important}.pageContainer.home .contentWrapper main{margin:0px !important}.pageContainer.home .contentWrapper main .image-text{position:absolute;top:40%;color:#ccfbb5;margin:0 auto;width:100%;text-align:center;padding:20px;text-shadow:3px 3px black}.pageContainer.home .contentWrapper main .image-text h1{font-size:3.5rem}.pageContainer.home .contentWrapper main .image-text h1,.pageContainer.home .contentWrapper main .image-text h4,.pageContainer.home .contentWrapper main .image-text h5{line-height:1;padding:5px}.pageContainer.home footer{display:none}

.standard-card-img{background-image:url("https://via.placeholder.com/1600x900");background-size:cover}.card{width:100%;background-position:center;background-size:cover;border:none;border-radius:0px}@media (min-width: 1200px){.card .item{padding:5rem 20rem !important}}.card .item{text-shadow:0px 2px #000000}.card .sky-gradient{background:linear-gradient(140deg, rgba(3,195,17,0.25098), rgba(0,82,183,0.368627)) !important}.card h1{font-size:3.2em}

hr{margin-top:1rem;margin-bottom:1rem;border:0;border-top:1px solid rgba(0,0,0,0.1)}

.carditem:nth-of-type(1) .card .card-title{color:#a94bff}.carditem:nth-of-type(1) .card .card-btn{background:#a94bff}.carditem:nth-of-type(2) .card .card-title{color:#ff5a5a}.carditem:nth-of-type(2) .card .card-btn{background:#ff5a5a}.rgba-dark{background-color:rgba(0,0,0,0.6) !important}

button{outline:0 !important;border:2px solid black}.board-row:not(:last-of-type) button{border-bottom:0}.board-row button:not(:last-of-type){border-right:0}

.board-wrapper{width:100%;padding-top:100%;position:relative}.board-wrapper .board{position:absolute;top:0;left:0;bottom:0;right:0}.board-wrapper .board .board-row{height:100%}.board-wrapper .board .board-row .square{align-items:center;display:inline-grid;justify-content:center;height:100%;font-size:3vw}.board-wrapper .board .board-row .square .square-content{display:flex;align-items:center}

i.fa{display:inline-block;border-radius:60px;padding:0.5em 0.6em}i.linkedin-bg{background-color:#0E76A8}i.github-bg{background-color:#211F1F}

.pageContainer{position:relative;min-height:100vh}.contentWrapper{padding-bottom:6rem}

